<template>
<div>
  <v-container fluid grid-list-md>
    <h2>{{message}}</h2>
      <v-form>
          <v-container>
            <v-layout row wrap>

              <v-flex xs12 sm6 md3>
                <v-text-field
                  label="First Name"
                  v-model="fname"
                  ref='fname'
                ></v-text-field>
              </v-flex>

              <v-flex xs12 sm6 md3>
                <v-text-field
                  label="Last Name"
                  v-model="lname"
                  ref='lname'
                  @keyup.enter="addPerson"
                ></v-text-field>
              </v-flex>

              <v-flex xs12 sm6 md3>
                <v-btn color="success" @click="addPerson">Add Person</v-btn>
              </v-flex>

            </v-layout>
            <v-layout row wrap>

              <v-flex xs12 sm6 md3>
                <v-text-field
                  label="Search"
                  v-model="searchText"
                  ref='search'
                  v-if="sortField === ''"
                ></v-text-field>
              </v-flex>  

              <v-flex xs12 sm6 md3>
                <p v-if="searchText === ''">Re-order by: 
                  <span @click="sortField='lname'" class="pointer">Last Name</span>;&nbsp; 
                  <span @click="sortField='fname'" class="pointer">First Name</span>;&nbsp;
                </p>
                <p v-if="sortField != ''">
                  Ordered by {{sortFieldText[sortField]}}
                </p>
                <p v-if="searchText != '' || sortField != ''"><v-btn @click="sortField=''; searchText=''" class="pointer">Clear</v-btn></p>
              </v-flex>

            </v-layout>
          </v-container>
        </v-form>

      <input type="checkbox" @click="selectAllCheckboxs()" v-model="allCheckboxs"/>
      <v-btn small flat color="primary" dark v-if="arePeopleChecked" @click="removeCheckedItems">Remove all that are checked</v-btn>

      <v-list>
        <draggable :list="filteredList" class="dragArea" @change="orderChanged" :options="{animation:200, handle:'.myhandle'}">
        <div v-for="(item, index) in filteredList" :key="index" class="item">
          <v-list-tile>
            <div :class= "{ editing : item.editing }">
              <input type="checkbox" @click="toggleCheckbox(item)" v-model="item.checkbox"/>&nbsp;
                <v-tooltip bottom class="pointer" @dblclick="editMode(index)">
                  <span slot="activator" @dblclick="editMode(index)">{{item.fname}} {{item.lname}}</span>
                  <span>douple click to edit</span>
                </v-tooltip>
                &nbsp;
                <v-tooltip bottom class="pointer">
                  <span slot="activator"><v-icon  @click="removeItem(item)">delete</v-icon></span>
                  <span>click to delete</span>
                </v-tooltip>
                <v-tooltip bottom class="pointer">
                  <span slot="activator"><v-icon  @click="openDialog(item,index)">loyalty</v-icon></span>
                  <span v-if="item.tags.length">Click to edit tags - {{item.tags}}</span>
                  <span v-else>Click to add tags</span>
                </v-tooltip>
                <v-tooltip bottom class="move">
                  <span slot="activator"><v-icon class="myhandle">swap_vertical_circle</v-icon></span>
                  <span>Move up or down</span>
                </v-tooltip>

            </div>

            <input type=text 
              v-model="item.fname"
              @blur="doneEdit(index)"
              @keyup.enter="doneEdit(index)"
              :class= "{ editing : item.editing, hidden: !item.editing }"
            />
            <input type=text 
              v-model="item.lname"
              @blur="doneEdit(index)"
              @keyup.enter="doneEdit(index)"
              :class= "{ editing : item.editing, hidden: !item.editing }"        
              />
          </v-list-tile>
          <v-divider></v-divider>
        </div>

      </draggable>
      </v-list>

      <TagsDialog 
        :dialog=dialog 
        :tags=tags 
        :index=index 
        @closeDialog="dialog = false" 
        @saveTags="addTags($event)" 
        ><span class="headline">Searchable Tags for {{fname}}</span></TagsDialog>   
     

  <p>{{test}}</p>

    <!-- Checked: {{checkedList}}<br/>
    Unchecked: {{unCheckedList}}<br/> -->

  </v-container>
  </div>
</template>

<script>
import draggable from 'vuedraggable'
import TagsDialog from '../components/TagsDialog'

const STORAGE_KEY = "graceCommunity"

  export default {
    components: {
      TagsDialog,
      draggable
    },
    data(){
      return {
        message: "My List of People",
        fname: '',
        lname: '',
        tags: '',
        checkbox: false,
        allCheckboxs: false,
        list: [],
        tempList: [],
        searchText: '',
        dialog: false,
        item: {},
        index: null,
        sortField: '',
        sortFieldText: {
          lname: 'Last Name',
          fname: 'First Name'
        }
      }
    },
    methods: {
      updateLocalStorage: (l) => localStorage.setItem(STORAGE_KEY, JSON.stringify(l)),
      orderChanged: function(){
        this.updateLocalStorage(this.list)
      },
      addPerson: function(){
        let self = this
          let newPerson = {
              fname: this.fname,
              lname: this.lname,
              tags: this.tags,
              checkbox: false
            }
        // console.log(newPerson)    
        this.list.push(newPerson)
        this.updateLocalStorage(this.list)
        this.lname = ''    
        this.fname = ''    
        this.tags = ''
        this.editing = false    
        this.$refs.fname.focus()
      },
      removeItem(item) {
          let r = confirm("Are you sure?")
          if (r) {
            this.list.splice(this.list.indexOf(item), 1)
            this.updateLocalStorage(this.list)
          }
      },
      removeCheckedItems() {
        this.list = this.unCheckedList
        this.updateLocalStorage(this.list)
        this.allCheckboxs = false
      },
      editMode(index){
        this.list[index].editing = true
      },
      doneEdit(index){
        this.list[index].editing = false
        this.updateLocalStorage(this.list)
      },
      toggleCheckbox(item){
        item.checkbox = !item.checkbox
        this.updateLocalStorage(this.list)
      },
      selectAllCheckboxs(){
        let self = this
        this.filteredList.forEach(function(el) {
          el.checkbox = !self.allCheckboxs
        })
        localStorage.setItem(STORAGE_KEY, JSON.stringify(this.list))
      },
      addTags(obj){
        let a = this.list[obj.index]
        a.tags = a.tags
        this.updateLocalStorage(this.list)
        this.dialog = false
      },
      openDialog(item, index){
        this.dialog = true
        this.index = index
        this.tags = item.tags
        this.fname = item.fname
      },
      compare: function(a,b){
        if (a[this.sortField] < b[this.sortField]) {
          return -1
        }
        if (a[this.sortField] > b[this.sortField]) {
          return 1
        }
        return 0
      }
    },
    computed: {
      checkedList: function(){
        return this.filteredList.filter(el => el.checkbox)
      },
      unCheckedList: function(){
        return this.filteredList.filter(el => !el.checkbox)
      },
      arePeopleChecked: function(){
        return this.checkedList.length
      },
      filteredList : function (){
        if (this.sortField != '') {return this.list.sort(this.compare)}
        if (this.searchText != '') {
          return this.list.filter(e => {
            e.searchAble = e.lname + e.fname + e.tags
            return e.searchAble.toLowerCase().includes(this.searchText.toLowerCase())
            })
        } else {
          return this.list
        }
      },
      test: function(){
        return this.$store.getters.test
      }
    },
    created(){
        let tempList = JSON.parse(localStorage.getItem(STORAGE_KEY)) || []
        this.list = tempList.map(function(el) {
          let obj = Object.assign({}, el)
          obj.editing = false
          return obj;
        }) //adding an editing element to each object not persisting 
        console.log(this.list)
    }
  }
</script>

<style>
input {
  display:block;
}

input.hidden {
  display:none
}

input[type="checkbox"] {
  display:inline
}

div.editing {
  display:none
}

input.editing {
  display:inline
}

input.editingTags {
  display:inline
}

form input {
  display:inline
}

input.dialog {
  display: inline
}

.pointer {
  cursor: pointer
}

.hide {
  display:none
}

.myhandle {
  cursor: move
}
</style>


