<template>
  <v-dialog v-model="dialog" persistent max-width="600px">
          <v-card>
            <v-card-title>
              <span class="headline">Searchable Tags for {{index}} </span>
              <slot></slot>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12>
                    <v-text-field label="Tags - separate with commas" v-model="dialogItem.tags"  @keyup.enter="$emit('saveTags', dialogItem)"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="$emit('closeDialog')">Close</v-btn>
              <v-btn color="blue darken-1" flat @click="$emit('saveTags', dialogItem)">Save</v-btn>
            </v-card-actions>
          </v-card>
    </v-dialog>
</template>

<script>

export default {
  props: ['item','index','dialog','tags'],
  computed:{
    dialogItem: function(){
      let thisItem = {}
      thisItem.tags = this.tags
      thisItem.index = this.index
      return thisItem
    }
  }
}

</script>

