<template>
  <span>
    <v-navigation-drawer
      v-model="sideNav" temporary absolute
    >

      <v-toolbar flat class="transparent">
        <v-list class="pa-0">
          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img src="https://randomuser.me/api/portraits/men/85.jpg">
            </v-list-tile-avatar>

            <v-list-tile-content>
              <v-list-tile-title>Grace Community</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
      </v-toolbar>

      <v-list class="pt-0" dense>
        <v-divider></v-divider>

        <v-list-tile
          v-for="item in drawerMenuItems"
          :key="item.name"
          router :to="item.path"        
          >
          <!-- <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action> -->

          <v-list-tile-content>
            <v-list-tile-title>{{ item.name }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <v-toolbar>
      <v-toolbar-side-icon @click="sideNav = !sideNav"></v-toolbar-side-icon>
      <v-toolbar-title>gracecommunity.app</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-btn flat v-for="item in toolbarMenuItems">
         <router-link :to=item.path>{{item.name}}</router-link>
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    

  </span>
</template>

<script>
export default {
  name: "AppNavigation",
    data () {
    return {
      sideNav: false,
      menuItems: [
        {
          name: 'About',
          path: 'about',
          menus: ['toolbar','drawer','home'],
          auth: ['all']
        },
        {
          name: 'Home',
          path: '/',
          menus: ['toolbar','drawer','home'],
          auth: ['all']
        },
        {
          name: 'Chat',
          path: 'chat',
          menus: ['drawer','home'],
          auth: ['all']
        },
        {
          name: 'Calendar',
          path: 'calendar',
          menus: ['drawer','home'],
          auth: ['all']
        }
      ]
    }
  },
  methods: {
    goToRoute: function(){
    }
  },
  computed: {
    toolbarMenuItems: function() {
      let self = this 
      return self.menuItems.filter(e => e.menus.includes('toolbar'))
    },
    drawerMenuItems: function() {
      let self = this 
      return self.menuItems.filter(e => e.menus.includes('drawer'))
    },
    homeMenuItems: function() {
      let self = this 
      return self.menuItems.filter(e => e.menus.includes('home'))
    }
  }
}
</script>

